.LotteryModeGamePage {
  margin-top: 10%;
}

.myItIMg img {
  height: 100%;
  width: 100%;
  margin-right: 10px;
}

.LotteryModeGamePage h1 {
  font-family: Poligon;
  font-size: 22px;
  font-weight: 800;
  letter-spacing: 0em;
  text-align: left;
  color: #ffffff;
}

.LotteryModeGamePage h1 span {
  font-family: Poligon;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0em;
  text-align: left;
}

.LotteryModeGamePage h3 {
  font-family: Poligon;
  font-size: 14px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  color: #ffffff;
}

.LotteryModeGamePage p {
  font-family: Poligon;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #ffffff;
}

.firstLine {
  display: inline;
}

.stepsOfLotteryMode {
  margin-top: 0%;
}

.stepsOfLotteryMode h1 {
  text-align: center;
}

.stepsOfLotteryMode h3 {
  text-align: center;
}
.stepsOfLotteryMode p {
  text-align: center;
}

img.rightSideImg {
  display: block;
  margin-top: -32px;
  margin-left: 10%;
}

img.leftSideImg {
  display: block;
  margin-top: -15%;
  margin-left: 56%;
}

@media screen and (max-width: 1535px) {
  img.rightSideImg {
    display: block;
    margin-top: -113px;
    margin-left: 20%;
}
}

@media screen and (max-width: 1132px) {
  img.rightSideImg {
    display: none;
}

img.leftSideImg {
  display: none;
}
}