.RoadmapPage p {
  font-family: Quicksand;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: center;
  color: #fff;
}
.RoadmapPage h1 {
  font-family: Poligon;
  font-size: 42px;
  font-weight: 700;
  line-height: 63px;
  letter-spacing: 0em;
  text-align: center;
  color: #fff;
  margin-top: 20%;
}

.initialmylt {
  background-image: url(../../assets/images/Roadmap/bg1.png);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 5%;
}

.initialmylt h2 {
  font-family: Poligon;
  font-size: 24px;
  font-weight: 800;
  line-height: 36px;
  letter-spacing: 0em;
  text-align: center;
  color: #fff;
  margin-top: 10%;
}

.roadmapImg {
  height: 100%;
  width: 100%;
}

.roadmapDetails h3 {
  font-family: Poligon;
  font-size: 24px;
  font-weight: 800;
  line-height: 36px;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
}

.roadmapDetails p {
  font-family: Poligon;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
}

.line1 {
  padding-top: 2%;
  margin-left: -10%;
  margin-bottom: -10%;
}

.line2 {
  margin-top: -10%;
  margin-right: -10%;
  margin-bottom: -10%;
}

.line3{
    margin-top: -10%;
  margin-right: -10%;
  margin-bottom: -15%;
}

@media screen and (max-width: 899px) {
  .line1 {
    display: none;
  }
  .line2 {
    display: none;
  }
  .line3{
    display: none;
  }
}
