.video-react-has-started .video-react-control-bar {
  display: none;
}

.Homepage {
  color: #fff;
}

.visualMap {
  background-image: url(../../assets/images/Homepage/bg2.png);
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: 5%;
  margin-bottom: 5%;
}

.visualMap h1 {
  font-family: Gilroy-Bold;
  font-size: 61px;
  line-height: 71px;
  letter-spacing: 0em;
  text-align: center;
}

.visualMap p {
  font-family: Quicksand;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: center;
}

.stepsHomepage {
  background-image: url(../../assets/images/Homepage/nextBg.png);
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 5%;
}

.stepsHomepage h1 {
  font-family: Poligon;
  font-size: 52px;
  font-weight: 800;
  line-height: 68px;
  letter-spacing: 0em;
  text-align: center;
}

.stepsHomepage p {
  font-family: Poligon;
  font-size: 18px;
  font-weight: 400;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: center;
}

.steps h1 {
  font-family: Poligon;
  font-size: 22px;
  font-weight: 700;
  line-height: 35px;
  letter-spacing: 0em;
  text-align: center;
}
.steps p {
  font-family: Poligon;
  font-size: 18px;
  font-weight: 400;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: center;
}

.stepsLine img {
  margin-top: -160px;
  margin-left: -40px;
}

@media screen and (max-width: 1199px) {
  .stepsLine img {
    display: none;
  }
}

.Homepage button {
  font-family: Quicksand;
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: 0em;
  text-align: left;
  background: linear-gradient(180deg, #fc9259 0%, #d7555f 100%);
  border: 1px solid #000000;
  border-radius: 10px;
  padding: 20px 100px;
  color: #fff;
  cursor: pointer;
}

.winningTicket {
  background-image: url(../../assets/images/Homepage/bg3.png);
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: 5%;
  padding: 100px;
}

.winningTicket h1 {
  font-family: Poligon;
  font-size: 52px;
  font-weight: 700;
  line-height: 68px;
  letter-spacing: 0em;
  text-align: center;
}

.winningTicket p {
  font-family: Poligon;
  font-size: 18px;
  font-weight: 400;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: center;
}

.winningTicket img {
  height: 100%;
  width: 100%;
}

.cheaperTicket {
  margin-top: 15%;
  font-family: Poligon;
  font-size: 52px;
  font-weight: 700;
  line-height: 68px;
  letter-spacing: 0em;
  text-align: center;
}

.abtCheaperTicket {
  font-family: Poligon;
  font-size: 18px;
  font-weight: 400;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: center;
}

.cardsHomepage {
  margin: 20px;
}

.cardsNfts {
  background: linear-gradient(180deg, #fc9259 0%, #d7555f 100%);
  border-radius: 15px;
  width: 100%;
  float: left;

  padding: 20px;
}

.cardsNfts p {
  font-family: Poligon;
  font-size: 23px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
}

.cardsNfts h1 {
  font-family: Poligon;
  font-size: 29px;
  font-weight: 800;
  line-height: 32px;
  letter-spacing: 0em;
  text-align: left;
}

.outerSpace {
  background-image: url(../../assets/images/Homepage/bg4.png);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 2%;
  margin-bottom: -5%;
}

.outerSpace h1 {
  font-family: Poligon;
  font-size: 52px;
  font-weight: 700;
  line-height: 68px;
  letter-spacing: 0em;
  text-align: center;
}

.outerSpace p {
  font-family: Quicksand;
  font-size: 18px;
  font-weight: 400;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: center;
}

.outerSpace button {
  background: linear-gradient(180deg, #fc9259 0%, #d7555f 100%);
  padding: 20px 50px;
  color: #fff;
  border: none;
  cursor: pointer;
}

.lastIcons{
  margin-top: 3%;
}

.lastIcons img{
  margin: 10px;
  cursor: pointer;
}