@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");

@font-face {
  font-family: Gilroy-Bold;
  src: url(./../../assets/font/Gilroy-Bold.woff);
}

.ticketDetails {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-content: center;
  align-items: flex-start;
  color: #fff;
}

.ticketDetails h3 {
  margin-left: 50px;
  font-family: Gilroy-Bold;
  font-size: 32px;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
}

.ticketDetails h3 span {
  font-family: Quicksand;
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
}

.Lottery {
  margin-top: 20px;
}

.LotteryPrice {
  color: #fff;
  text-align: left;
}

.Lottery img {
  height: 100%;
  width: 70%;
}

.LotteryPrice h3 {
  font-family: Bebas Neue;
  font-size: 72px;
  font-weight: 400;
  line-height: 86px;
  letter-spacing: 0em;
  text-align: left;
}
.LotteryPrice p {
  font-family: Quicksand;
  font-size: 22px;
  font-weight: 400;
  line-height: 33px;
  letter-spacing: 0em;
  text-align: left;
}

.IncDecInput {
  margin-top: 20px;
}

.IncDecInput input {
  padding: 7.5px 14px;
  width: 25%;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.17);
  border: 3px solid #9a08c8;
  border-radius: 7px;
  margin: 10px;
  text-align: center;
  font-family: "Gilroy-Bold";
  font-size: 24px;
  /* identical to box height, or 36px */
  color: #ffffff;
}

.IncDecInput button {
  background: rgba(255, 255, 255, 0.17);
  border: 3px solid #9a08c8;
  border-radius: 50%;
  padding: 10px 20px;
  font-family: "Gilroy-Bold";
  font-size: 24px;
  /* identical to box height, or 36px */
  color: #ffffff;
  margin: 10px;


}

.BuyYouMint {

  background: #922f4b;
  color: #fff;
  border-radius: 12px;
  padding: 16px 20px;
  font-family: "Quicksand" ;
  font-size: -webkit-calc(100% * 3 - 6.666666669px);
  font-size: calc(100% * 3 - 6.666666669px);
  font-weight: 700 ;
  line-height: 41px;
  letter-spacing: 0em;
  text-align: center;
  border: none;
}

.BuyYouMint:hover{
  background: #931b3d;
}


@media screen and (max-width: 599px) {
 
.Lottery img {
  width: 35%;
}

.LotteryPrice h3 {
  font-size: 35px;
  line-height: 0px;
  letter-spacing: 0em;
  text-align: center;
}

.LotteryPrice p {
  font-size: 22px;
  line-height: 15px;
  text-align: center;
}

.ticketDetails{
  margin-top: -5%;
}

.IncDecInput{
margin-top: 0;
}
}