summary {
  display: block;
  position: relative;
  font-size: 1.33em;
  font-weight: bold;
  cursor: pointer;

  font-family: Quicksand;
  font-size: 22px;
  font-weight: 700;
  line-height: 33px;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
}
summary::before,
summary::after {
  width: 0.75em;
  height: 2px;
  position: absolute;
  top: 50%;
  right: 0;
  content: "";
  background-color: currentColor;
  text-align: right;
  transform: translateY(-50%);
  transition: transform 0.2s ease-in-out;
}
summary::after {
  transform: translateY(-50%) rotate(90deg);
}
[open] summary::after {
  transform: translateY(-50%) rotate(180deg);
}
summary::-webkit-details-marker {
  display: none;
}

.FAQ_answer {
  font-family: Quicksand;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
}

.para {
  font-family: Quicksand;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: center;
  color: #fff;
}

.FAQS_Questions {
  margin-top: 10%;
}

details {
  border-bottom: 2px dotted #fff;
  padding-top: 3%;
  padding-bottom: 3%;
}

.checkoutPaper {
  margin-top: 10%;
  background: linear-gradient(261.08deg, #43122f 43.22%, #601c48 73.03%);
  border-radius: 20px;
  padding-top: 5%;
  padding-bottom: 5%;

}

.checkoutPaper p {
  font-family: Quicksand;
  font-size: 22px;
  font-weight: 100;
  line-height: 26px;
  letter-spacing: 0em;
  text-align: center;
  color: #fff;
}

.checkoutPaper button {
  background: #922f4b;
  border-radius: 10px;
  padding: 10px 25px;

  border: none;
  font-family: Quicksand;
  font-size: 31px;
  font-weight: 700;
  line-height: 39px;
  letter-spacing: 0em;
  text-align: center;
  color: #fff;
}

.checkoutPaper button:hover{
  background: #bf2d57;
  cursor: pointer;
}