.CarsaulCard {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 16px;

  border: 2px solid #d7555f;

  padding: 20px;

  margin: 20px;
}

.CarsaulCard img {
  height: 100%;
  width: 100%;
}

.accountNamenShare {
  display: flex;
  justify-content: space-between;
}

.accountNamenShare p {
  font-family: Poligon;
  font-size: 18px;
  font-weight: 400;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: left;
}

.accountNamenShare img {
    height: 25%;
    margin: 5px;
    width: 89%;
}

.accountNamenShare .icons {
  justify-content: space-between;
  display: flex;
  align-content: space-between;
  align-items: center;
  color:  #fc9259;
}
